body::before {
    content: '';
    height: 50vh;
    width: 100%;
    position: fixed;
    left: 0;
    background: #1c2020;
    z-index: 6;
    top: 0%;
    -webkit-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
        transform: translateY(-100%);
}
body::after {
    content: '';
    height: 50vh;
    width: 100%;
    position: fixed;
    left: 0;
    background: #1c2020;
    z-index: 6;
    bottom: 0%;
    -webkit-transform: translateY(100%);
    -ms-transform: translateY(100%);
        transform: translateY(100%);
}
.home-web {
    width: 100%;
    z-index: 5;
}
.home-web-wrapper {
    background-image: -webkit-radial-gradient(51% 75%, circle, #0d2339, #000000);
    background-image: -o-radial-gradient(51% 75%, circle, #0d2339, #000000);
    background-image: radial-gradient(circle at 51% 75%, #0d2339, #000000);
    width: 100%;
    height: 100%;
    min-height: 500px;
    min-width: 550px;
    position: relative;
}
.home-wrapper {
    width: 100%;
    height: 100%;
}
.home-mobile {
    overflow-y: hidden;
    width: 100%;
    height: 100%;
}
.home-web {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
    top: 50%;
    -webkit-transform: translate(0 ,-50%);
        -ms-transform: translate(0 ,-50%);
            transform: translate(0 ,-50%);
}
.err-msg {
    color: white;
    font-size: 17px;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 100;
    direction: rtl;
    font-family: Heebo, 'Alef', sans-serif;
}

@-webkit-keyframes come-from-bottom {
    from {
        bottom: -500px;
    }
    to {
        bottom:0px;
        opacity: 1;
    }
}

@keyframes come-from-bottom {
    from {
        bottom: -500px;
    }
    to {
        bottom:0px;
        opacity: 1;
    }
}
.img-man-second, .img-man-third {
    position: relative;
    opacity: 0;
    -webkit-animation: come-from-bottom 1.5s ease-out 1s;
            animation: come-from-bottom 1.5s ease-out 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.img-man-second {
    margin-left:30px;
    margin-right:150px;
}
.img-man-third {
    margin-left:150px;
    margin-right:30px;
}
@media (max-width: 900px) {
    .img-man-second {
        margin-left:30px;
        margin-right:30px;
    }
    .img-man-third {
        margin-left:30px;
        margin-right:30px;
    }
}
.img-man-first {
    position: relative;
    opacity: 0;
    -webkit-animation: come-from-bottom 1.5s ease-out;
            animation: come-from-bottom 1.5s ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.images-wrapper {
    position: absolute;
    bottom:0;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 100%;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: baseline;
        align-items: baseline;
}


